* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4 {
  color: rgb(59, 59, 59);
  font-family: 'Open Sans', sans-serif;
}

.app {
  min-height: 100vh;
  background: linear-gradient(to right, #b2fefa, #0ed2f7);
  display: flex;
  align-items: center;
  justify-content: center;
}

.glass-container {
  background: white;
  min-height: 90vh;
  width: 75%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.6),
    rgba(255, 255, 255, 0.3)
  );
  border-radius: 2rem;
  z-index: 2;
  backdrop-filter: blur(15px);
}

.library-open {
  transition: all 0.5s ease;
}

.library-active {
  margin-left: 20%;
}

.circle-one,
.circle-two {
  background: white;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.6),
    rgba(255, 255, 255, 0.4)
  );
  height: 20rem;
  width: 20rem;
  border-radius: 50%;
  position: absolute;
}
.circle-one {
  top: 13%;
  right: 9%;
}
.circle-two {
  bottom: 12%;
  left: 4%;
}

@media screen and (max-width: 500px) {
  .glass-container {
    width: 100vh;
    height: 100vh;
    border-radius: 0px;
    padding-top: 1rem;
  }
}

@media screen and (max-width: 1000px) {
  .circle-one,
  .circle-two {
    height: 17rem;
    width: 17rem;
  }
}

@media screen and (max-width: 600px) {
  .circle-one,
  .circle-two {
    height: 10rem;
    width: 10rem;
  }
}

@media screen and (max-width: 350px) {
  .circle-one,
  .circle-two {
    display: none;
  }
}

@import './nav';
@import './player';
@import './song';
@import './library';
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap');
