nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  button {
    background: transparent;
    border: none;
    cursor: pointer;
    border: 2px solid rgb(32, 32, 32);
    padding: 0.5rem;
    transition: all 0.3s ease;
    &:hover {
      background: rgb(32, 32, 32);
      color: white;
    }
  }
}

button:focus {
  outline: none;
}

@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 6;
    }
  }
}
