@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}h1,h2,h3,h4{color:#3b3b3b;font-family:'Open Sans', sans-serif}.app{min-height:100vh;background:linear-gradient(to right, #b2fefa, #0ed2f7);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.glass-container{background:white;min-height:90vh;width:75%;background:linear-gradient(to right bottom, rgba(255,255,255,0.6), rgba(255,255,255,0.3));border-radius:2rem;z-index:2;-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px)}.library-open{transition:all 0.5s ease}.library-active{margin-left:20%}.circle-one,.circle-two{background:white;background:linear-gradient(to right bottom, rgba(255,255,255,0.6), rgba(255,255,255,0.4));height:20rem;width:20rem;border-radius:50%;position:absolute}.circle-one{top:13%;right:9%}.circle-two{bottom:12%;left:4%}@media screen and (max-width: 500px){.glass-container{width:100vh;height:100vh;border-radius:0px;padding-top:1rem}}@media screen and (max-width: 1000px){.circle-one,.circle-two{height:17rem;width:17rem}}@media screen and (max-width: 600px){.circle-one,.circle-two{height:10rem;width:10rem}}@media screen and (max-width: 350px){.circle-one,.circle-two{display:none}}nav{min-height:10vh;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}nav button{background:transparent;border:none;cursor:pointer;border:2px solid #202020;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background:#202020;color:white}button:focus{outline:none}@media screen and (max-width: 768px){nav button{z-index:6}}.player{min-height:20vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}.time-control{width:50%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.time-control input{width:100%;-webkit-appearance:none;appearance:none;background:transparent;cursor:pointer}.time-control p{padding:1rem}.play-control{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;padding:1rem 0rem;width:40%}.play-control svg{cursor:pointer}input[type='range']:focus{outline:none}input[type='range']::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;height:16px;width:16px}.tracker{width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden}.animate-tracker{background:#ebebeb;width:100%;height:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(0%);transform:translateX(0%);pointer-events:none}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:60%}}.song-container{min-height:50vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.song-container img{width:25%;border-radius:50%}.song-container h3{padding:2rem 1rem 1rem;font-size:2rem}.song-container h2{font-size:1rem}@media screen and (max-width: 768px){.song-container img{width:60%}}@-webkit-keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.rotateImg{-webkit-animation:spin 25s linear infinite;animation:spin 25s linear infinite}@media screen and (max-width: 400px){.song-container h3{font-size:1.5rem}.song-container h2{font-size:1rem}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;z-index:3;background:#c4f7fb;box-shadow:2px 2px 50px #c8f5fd;overflow-y:scroll;overflow-x:hidden;border-radius:2rem;-webkit-transform:translateX(-200%);transform:translateX(-200%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library-song{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1rem 2rem;cursor:pointer;transition:background 0.6s ease}.library-song img{width:30%}.library-song:hover{background:#5ce7f8}.song-description{padding-left:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.song-description h3{font-size:1rem}.song-description h4{font-size:0.8rem}*{scrollbar-width:thin;scrollbar-color:rgba(92,231,249,0.7) transparent}*::-webkit-scrollbar{width:4px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(92,231,249,0.7);border-radius:20px;border:transparent}.selected{background:#5ce7f8}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 900px){.library{width:100%;border-radius:0px}}

