.song-container {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    width: 25%;
    border-radius: 50%;
  }
  h3 {
    padding: 2rem 1rem 1rem;
    font-size: 2rem;
  }
  h2 {
    font-size: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .song-container {
    img {
      width: 60%;
    }
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.rotateImg {
  animation: spin 25s linear infinite;
}

@media screen and (max-width: 400px) {
  .song-container {
    h3 {
      font-size: 1.5rem;
    }
    h2 {
      font-size: 1rem;
    }
  }
}
