.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  z-index: 3;
  background: #c4f7fb;
  box-shadow: 2px 2px 50px #c8f5fd;
  overflow-y: scroll;
  overflow-x: hidden;
  border-radius: 2rem;
  transform: translateX(-200%);
  transition: all 0.5s ease;
  opacity: 0;
  h2 {
    padding: 2rem;
  }
}

.library-song {
  display: flex;
  align-items: center;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: background 0.6s ease;
  img {
    width: 30%;
  }
  &:hover {
    background: #5ce7f8;
  }
}

.song-description {
  padding-left: 1rem;
  display: flex;
  flex-direction: column;
  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: 0.8rem;
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(92, 231, 249, 0.7) transparent;
}

*::-webkit-scrollbar {
  width: 4px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(92, 231, 249, 0.7);
  border-radius: 20px;
  border: transparent;
}

.selected {
  background: #5ce7f8;
}

.active-library {
  transform: translateX(0%);
  opacity: 1;
}

@media screen and (max-width: 900px) {
  .library {
    width: 100%;
    border-radius: 0px;
  }
}
